<template>
  <div class="header">
    <div class="header-left">
      <div class="iconfont back-icon">&#xe624;</div>
    </div>
    <div class="header-input">
      <span class="iconfont">&#xe632;</span>
      输入城市/景点/游玩
    </div>
    <router-link to="/city">
      <div class="header-right">
        {{this.city}}
        <span class="iconfont arrow-icon">&#xe64a;</span>
      </div>
    </router-link>
  </div>
</template>

<script>
import { mapState } from 'vuex'
export default {
  name: 'HomeHeader',
  computed: {
    ...mapState(['city'])
  }
}
</script>

<style lang="stylus" scoped>
  @import '~styles/varibles.styl'
  .header
    display: flex;
    line-height: $headerHeight;
    background: $bgColor;
    color: #fff;

    .header-left
      width: .64rem;
      float: left;
      .back-icon
        text-align: center;
        font-size: .4rem;
    .header-right
      color: #fff;
      min-width: 1.04rem;
      padding: 0 .1rem;
      float: right;
      text-align: center;
      .arrow-icon{
        font-size: .24rem;
      }
    .header-input
      flex: 1;
      padding-left: .2rem;
      background: #fff;
      border-radius: .1rem;
      margin-top: .12rem;
      margin-left:.2rem;
      height: .64rem;
      line-height: .64rem;
      color: #ccc;
</style>



